textarea{
    outline: none;
    resize:vertical;
    width: 60%;
    padding: 20px;
    border-radius: 15px 0px;
    background-color: gainsboro;
    
}
textarea:focus{
    box-shadow: 0px 0px 3px 3px purple;
}
.box{
    width: 60%;
    padding: 20px;
    border-radius: 15px 0px;
    background-color: gainsboro;
    color: black;
    text-align: left;
    margin: auto;
    width: auto;
    max-width: 60%;
    height: 550px;
    overflow-x: hidden;
    overflow-y: scroll;
}
.box code{
    font-size: 12px;
    font-weight: bold;
    background-color: blanchedalmond;
    padding: 2px 2px;
    border-radius: 4px;
}
.box pre{
    background-color: blanchedalmond;
    padding: 10px;
    border-radius: 5px;
}