@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  margin: 0;
  background-color: rgb(48, 48, 48);
  color: #dadada;
  text-align: center;
}
textarea{
    outline: none;
    resize:vertical;
    width: 60%;
    padding: 20px;
    border-radius: 15px 0px;
    background-color: gainsboro;
    
}
textarea:focus{
    box-shadow: 0px 0px 3px 3px purple;
}
.Previewer_box__2QY5Q{
    width: 60%;
    padding: 20px;
    border-radius: 15px 0px;
    background-color: gainsboro;
    color: black;
    text-align: left;
    margin: auto;
    width: auto;
    max-width: 60%;
    height: 550px;
    overflow-x: hidden;
    overflow-y: scroll;
}
.Previewer_box__2QY5Q code{
    font-size: 12px;
    font-weight: bold;
    background-color: blanchedalmond;
    padding: 2px 2px;
    border-radius: 4px;
}
.Previewer_box__2QY5Q pre{
    background-color: blanchedalmond;
    padding: 10px;
    border-radius: 5px;
}
